<template>
  <div>
    <panel title="ประวัติส่วนตัวพนักงาน" class="mb-5">
      <div class="vx-row">
        <div class="vx-col w-3/12">
          <attached
            :attached_id="form.id"
            model="App\\Models\\User"
            :readOnly="true"
            ref="upload"
          ></attached>
        </div>
        <div class="vx-col w-9/12">
          &nbsp;
          <div class="vx-row my-5">
            <div class="vx-col w-1/12"><b>ชื่อ-สกุล</b></div>
            :
            <div class="vx-col w-2/12">{{ form.name }}</div>

            <div class="vx-col w-2/12"><b>รหัสประจำตัว</b></div>
            :
            <div class="vx-col w-2/12">{{ form.code }}</div>

            <div class="vx-col w-1/12"><b>ตำแหน่ง</b></div>
            :
            <div class="vx-col w-2/12">{{ form.position }}</div>
          </div>
          &nbsp;
          <div class="vx-row">
            <div class="vx-col w-1/12"><b>สิทธิ์</b></div>
            :
            <div class="vx-col w-2/12">{{ form.position }}</div>
          </div>
        </div>
      </div>

      <div class="vx-row">
        <div class="vx-col ml-5" vs-type="flex" vs-justify="flex-start">
          <vs-button color="primary" type="filled" @click.stop="editData(form)"
            >แก้ไข</vs-button
          >&nbsp;
          <router-link to="/employee/Index"
            ><vs-button color="danger" type="filled"
              >ยกเลิก</vs-button
            ></router-link
          >&nbsp;
        </div>
      </div>
    </panel>

    <panel title="ประวัติการใช้งาน">
      <vs-table pagination max-items="5" search :data="users">
        <template slot="header"> </template>
        <template slot="thead">
          <vs-th></vs-th>
        </template>
        <template slot-scope="{ data }">
          <vs-tr :key="user" v-for="(users, user) in data">
            <vs-td>
              {{ data[user].code }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>
      <br />
    </panel>

  </div>
</template>

<script>
export default {
  data () {
    return {
      checkBox1: ['luis'],
      form: {
        id: '',
        name: '',
        tel: '',
        address: ''
      },
      users: []
    }
  },
  methods: {
    setData () {
      this.form.id = this.$route.query.id
    },
    async loadData () {
      try {
        const { data } = await this.$http.get(
          `/api/user/${this.form.id}`
        )
        this.form = data.data
      } catch (error) {
        throw error
      }
    },
    editData (user) {
      this.$router.push({ name: 'user_edit', query: { id: user.id } })
    }
  },
  mounted () {
    this.setData()
    this.loadData()
  }
}
</script>

<style>
.pt {
  height: 110px;
  margin: auto;
}
</style>
